class FrameGroup {
  parent?: FrameGroup;
  prev?: FrameGroup;
  next?: FrameGroup;
  children?: FrameGroup[];
  charId?: string;
  displayName?: string;
  text?: string;

  constructor(parent?: FrameGroup) {
    if (parent) {
      this.charId = parent.charId;
      this.displayName = parent.displayName;
    }
  }

  static mkframes(data: FrameGroup[]): FrameGroup[] {
    const result = this.buildFrames(data);
    return result;
  }

  static buildFrames(data: FrameGroup[], parent?: FrameGroup, aResult?: FrameGroup[]): FrameGroup[] {
    let current: FrameGroup;
    const result: FrameGroup[] = aResult ? aResult : [];

    data.forEach(frame => {
      if (parent) {
        if (!frame.charId) frame.charId = parent.charId;
        if (!frame.displayName) frame.displayName = parent.displayName;
      }
      if (frame.text) {
        current = frame;
        if (result.length > 0) {
          const latest = result[result.length - 1];
          latest.next = current;
          current.prev = latest;
        }
        result.push(current);
      } else {
        current = undefined;
      }
      if (frame.children) {
        this.buildFrames(frame.children, frame, result);
        // .forEach(child => result.push(child));
      }
    });
    return result;
  }

  static mkchars(data: FrameGroup[]) {
    const result: CharGroup[] = [];
    data.forEach(frame => {
      if (frame.text) {
        const sthg = frame.text.replace(/a/, '');
        const chg = new CharGroup();
        chg.text = sthg;
        result.push(chg);
      }
    });
    return result;
  }
}

class CharGroup {
  text: string = '';
}

export { FrameGroup };



